import React from 'react';
import {BrowserRouter, Router, Link} from 'react-router-dom';
import ReactDOM from 'react-dom';
import {createMuiTheme, makeStyles, MuiThemeProvider} from '@material-ui/core/styles';
import AppContext from './AppContext'
import {
    AppBar, Hidden, BottomNavigation, Box, Button, Paper,
    Container, Dialog, MenuList, MenuItem, FormLabel,
    DialogActions, DialogContent,DialogTitle,
    Grid, List, ListItem, Typography, Drawer, ListItemIcon, ListItemText, Divider
} from '@material-ui/core';
import Cart from './components/service/Cart.js'
import Site from './components/service/Site.js'
import Toast from './components/Toast'
import './App.css';
import BottomBar from "./components/BottomBar";
import CartDialog from "./components/CartDialog";
import DeliveryDialog from "./components/DeliveryDialog";
import RegisterCustomer from "./components/RegisterCustomer";
import LoginDialog from "./components/LoginDialog";
import ProfileDialog from "./components/ProfileDialog";
import MyAppBar from "./components/MyAppBar";
import PaymentDialog from "./components/PaymentDialog";
import FoodOptionsDialog from "./components/FoodOptionsDialog";
import OfferOptionsDialog from "./components/OfferOptionsDialog";
import InboxIcon from "@material-ui/icons/Inbox";
import MailIcon from "@material-ui/icons/Mail";
import Util from "./components/service/Util";
import LeftDrawer from "./components/LeftDrawer";
import OrderHistoryDialog from "./components/OrderHistoryDialog";
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import TelegramIcon from '@material-ui/icons/Telegram';
import YouTubeIcon from '@material-ui/icons/YouTube';
import ForgotPassDialog from "./components/ForgotPassDialog";
import MessageBox from "./components/MessageBox";
import WaitingProgress from "./components/WaitingProgress";

const themeLight = createMuiTheme({
    type: 'light',
    palette: {
        error: {
            main: "#ff604f"
        }
    },
    typography: {
        fontFamily: [
            'IRANSansMobileFaNum',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif'
        ].join(','),
    }
});
const themeDark = createMuiTheme({
    type: 'dark',

});
const useStyles = makeStyles((theme) => ({
    toolbar: {
        flexGrow: 1,
        color: 'white',
    },
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));

class App extends React.Component {
    user = {}
    classes = {};
    constructor(props) {
        super(props);
        AppContext.app = this;
        this.state = {
            fatalError: undefined,
        };
        this.classes = useStyles;
    }

    reload(){
        window.location.reload();
    }
    componentDidMount() {
        Site.instance.init();

    }

    componentWillMount() {
    }

    render() {
        let site = Site.instance.site;
        return (
                <BrowserRouter>
                    <MuiThemeProvider theme={site && site.appTheme === 'dark' ? themeDark : themeLight}>
                        <div id="mainContainer" style={{position:"fixed",top:0, left:0, width:"100%", height:'100vh',backgroundSize: "cover",
                            backgroundPosition: "center",
                            backgroundColor:"#555555",
                            zIndex: -9999
                        }}></div>
                        <Container  style={{padding: "0",
                            minHeight: "100%",
                            height: "auto !important",
                            margin: "0 auto -142px",
                            maxWidth: '1224px'
                        }} >
                                {
                                    this.state.fatalError &&
                                    <Dialog open={true}>
                                        <DialogTitle>
                                            Error
                                        </DialogTitle>
                                        <DialogContent>
                                                <Typography component={"h5"} style={{  textAlign: "center"}}>
                                                    {this.state.fatalError}
                                                </Typography>
                                        </DialogContent>
                                        <DialogActions>
                                            <Button color="primary" onClick={()=>{
                                                this.setState({fatalError: null})
                                            }} >Cancel</Button>
                                            <Button color="primary" onClick={()=>{
                                                this.reload();
                                            }} >Reload</Button>

                                        </DialogActions>
                                    </Dialog>

                                }
                            { false &&    <MyAppBar/> }
                                <RegisterCustomer />

                                <MessageBox/>
                                <WaitingProgress/>
                                <Toast/>
                            <div style={{height:142}}></div>
                        </Container>
                        <div style={{height1:142, minHeight:142, width:'100%', backgroundColor:"rgb(77 77 77 / 51%)", zIndex: 9999}} className="footer">
                            <Grid item xs={12} style={{maxWidth: '1224px',margin: "0 auto", padding: 15, textAlign: "center"}}>
                                <Typography style={{fontWeight:"bold", color:"white"}} >
                                    FinalVPN
                                </Typography>
                                <FacebookIcon size="2x"/>
                                <a href={"https://t.me/FinalVPN"} target={"_blank"}><TwitterIcon size="2x"/></a>
                                <a href={"https://www.youtube.com/@finalvpn"} target={"_blank"}><YouTubeIcon size="2x"/></a>
                                <a href={"https://t.me/FinalVPN_App"} target={"_blank"}><TelegramIcon size="2x"/></a>
                                <div style={{color: "dimgray", fontSize:"12px"}}><a href="https://finalvpn.github.io" target="_blank">Download App</a></div>
                            </Grid>
                        </div>
                        <Hidden mdUp>
                        <BottomBar />
                        </Hidden>
                    </MuiThemeProvider>
                </BrowserRouter>
        );
    }
}
export default App;
